<template>
  <v-dialog v-model="showDialog" persistent max-width="1000">
    <v-card class="">
      <div class="d-flex align-center">
        <v-card-title class="text-h5">Chi tit n hng</v-card-title>
        <v-spacer></v-spacer>
        <div class="mr-2">
          <v-btn icon @click="close()">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </div>
      </div>
      <div class="d-flex justify-space-between">
        <div class="px-4">
          <div><strong>M n hng: </strong> {{ order.id }}</div>
          <div><strong>Ngy to n: </strong> {{ order.createdAt }}</div>
          <div><strong>Trng thi: </strong> {{ order.status }}</div>
          <div>
            <strong>Tng tin: </strong>
            {{ order.totalPrice.toLocaleString() }} 
          </div>
          <div><strong>Gim gi: </strong> {{ order.promotionPercent }}%</div>
          <div>
            <strong>Thanh ton: </strong
            >{{ order.totalPaid.toLocaleString() }} 
          </div>
        </div>
        <v-btn
          :loading="isLoading"
          v-if="order.status == 'Pending' || order.status == 'Deliveried'"
          depressed
          height="36"
          class="mr-4"
          color="rgba(254, 52, 100)"
          @click="cancelOrder"
        >
          <div class="text-body-2 font-weight-bold white--text">Hu n</div>
        </v-btn>
      </div>
      <div class="my-2">
        <v-data-table
          :headers="headers"
          :items="order.items"
          :items-per-page="5"
          class="elevation-1"
        >
          <template v-slot:item.imageUrl="{ item }">
            <v-img class="ma-2" width="48" height="48" :src="item.imageUrl">
            </v-img>
          </template>
          <template v-slot:item.price="{ item }">
            <span>{{ item.price.toLocaleString() }} </span>
          </template>
          <template v-slot:item.total="{ item }">
            <span>{{ (item.price * item.quantity).toLocaleString() }} </span>
          </template>
        </v-data-table>
      </div>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  data() {
    return {
      isLoading: false,
      showDialog: true,
      headers: [
        {
          text: '',
          align: 'start',
          sortable: false,
          value: 'imageUrl',
        },
        { text: 'M SKU', value: 'sku' },
        { text: 'Tn sn phm', value: 'name' },
        { text: 'n gi', value: 'price' },
        { text: 'S lng', value: 'quantity' },
        { text: 'Thnh tin', value: 'total' },
      ],

      isCanceled: false,
    };
  },

  props: {
    order: {
      type: Object,
      default: () => {
        return {};
      },
    },
  },

  methods: {
    async cancelOrder() {
      try {
        this.isLoading = true;
        const response = await this.$http.put(
          `/orders/${this.order.id}/status/cancel`
        );
        if (response.success) {
          this.$notify.success('Hu n hng thnh cng');
          this.isCanceled = true;
          this.close();
        }
      } catch (error) {
        console.log(error);
      } finally {
        this.isLoading = false;
      }
    },

    close() {
      this.$emit('closeOrderDetail', this.isCanceled);
    },
  },
};
</script>

